/* App.css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #0a0a0f;
  min-height: 100vh;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: #0a0a0f;
}

/* Header */
.app-header {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.app-header h1 {
  color: #333;
  font-size: 1.8rem;
  font-weight: 700;
}

.header-info {
  display: flex;
  gap: 1.5rem;
  align-items: center;
}

.status-indicator {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 500;
  color: #555;
}

.demographics-indicator,
.fire-indicator {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 500;
  color: #555;
  font-size: 0.9rem;
}

.status-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  animation: pulse 2s infinite;
}

.status-dot.connected {
  background-color: #10b981;
}

.status-dot.disconnected {
  background-color: #ef4444;
}

@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
}

/* Navigation */
.navigation {
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
  padding: 1rem 2rem;
  display: flex;
  gap: 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.navigation button {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 25px;
  background: transparent;
  color: #666;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.navigation button:hover {
  background: rgba(102, 126, 234, 0.1);
  color: #667eea;
}

.navigation button.active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

/* Main Content */
.main-content {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
}

.main-content.fullscreen {
  padding: 0;
  overflow: hidden;
}

/* Dashboard */
.dashboard-grid {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
  height: 100%;
}

.video-section, .info-section {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.video-section h2 {
  margin-bottom: 1.5rem;
  color: #333;
  font-size: 1.5rem;
}

.video-container {
  background: #000;
  border-radius: 15px;
  overflow: hidden;
  margin-bottom: 1.5rem;
  position: relative;
  aspect-ratio: 16/9;
}

.video-frame {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.no-video {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #888;
  text-align: center;
}

.no-video p:first-child {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.video-controls {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
}

.mode-selector h3 {
  margin-bottom: 1rem;
  color: #555;
}

.mode-buttons {
  display: flex;
  gap: 0.5rem;
}

/* Buttons */
.btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 25px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

.btn-secondary {
  background: #6b7280;
  color: white;
}

.btn-secondary:hover:not(:disabled) {
  background: #4b5563;
  transform: translateY(-2px);
}

.btn-outline {
  background: transparent;
  color: #667eea;
  border: 2px solid #667eea;
}

.btn-outline:hover {
  background: #667eea;
  color: white;
}

.btn-active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

/* Info Section */
.info-section {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.status-card h3 {
  margin-bottom: 1rem;
  color: #333;
}

.status-items {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.status-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem;
  background: rgba(102, 126, 234, 0.05);
  border-radius: 10px;
}

.status-active {
  color: #10b981;
  font-weight: 600;
}

.status-inactive {
  color: #ef4444;
  font-weight: 600;
}

/* Recent Logs */
.recent-logs h3 {
  margin-bottom: 1rem;
  color: #333;
}

.logs-container {
  max-height: 300px;
  overflow-y: auto;
}

.log-item {
  display: flex;
  flex-direction: column;
  padding: 0.75rem;
  border-left: 4px solid #667eea;
  background: rgba(102, 126, 234, 0.05);
  border-radius: 0 10px 10px 0;
  margin-bottom: 0.5rem;
}

.log-item.face {
  border-left-color: #10b981;
  background: rgba(16, 185, 129, 0.05);
}

.log-item.plate {
  border-left-color: #f59e0b;
  background: rgba(245, 158, 11, 0.05);
}

.log-time {
  font-size: 0.8rem;
  color: #888;
  margin-bottom: 0.25rem;
}

.log-content {
  font-weight: 500;
  color: #333;
}

.authorized {
  color: #10b981;
}

.unauthorized {
  color: #ef4444;
}

.no-logs {
  text-align: center;
  color: #888;
  padding: 2rem;
}

/* Forms */
.registration-form {
  max-width: 600px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.registration-form h2 {
  margin-bottom: 2rem;
  color: #333;
  text-align: center;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: #555;
  font-weight: 500;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 2px solid #e5e7eb;
  border-radius: 10px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.checkbox-group {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.checkbox-group input[type="checkbox"] {
  width: auto;
  margin-right: 0.5rem;
}

.message {
  padding: 1rem;
  border-radius: 10px;
  margin-bottom: 1.5rem;
  font-weight: 500;
}

.message.success {
  background: rgba(16, 185, 129, 0.1);
  color: #059669;
  border: 1px solid rgba(16, 185, 129, 0.2);
}

.message.error {
  background: rgba(239, 68, 68, 0.1);
  color: #dc2626;
  border: 1px solid rgba(239, 68, 68, 0.2);
}

.info-box {
  background: rgba(59, 130, 246, 0.1);
  border: 1px solid rgba(59, 130, 246, 0.2);
  border-radius: 10px;
  padding: 1.5rem;
  margin-top: 2rem;
}

.info-box h3 {
  margin-bottom: 1rem;
  color: #1d4ed8;
}

.info-box code {
  background: rgba(0, 0, 0, 0.05);
  padding: 0.5rem;
  border-radius: 5px;
  display: block;
  margin-top: 1rem;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
}

/* Logs Section */
.logs-section {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.logs-section h2 {
  margin-bottom: 2rem;
  color: #333;
}

.logs-controls {
  margin-bottom: 2rem;
}

.filter-buttons {
  display: flex;
  gap: 0.5rem;
}

.logs-table {
  background: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
}

.table-header {
  display: grid;
  grid-template-columns: 150px 120px 1fr 120px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  font-weight: 600;
  padding: 1rem;
}

.table-row {
  display: grid;
  grid-template-columns: 150px 120px 1fr 120px;
  padding: 1rem;
  border-bottom: 1px solid #f3f4f6;
  align-items: center;
}

.table-row:last-child {
  border-bottom: none;
}

.table-row:hover {
  background: rgba(102, 126, 234, 0.05);
}

.confidence {
  background: rgba(102, 126, 234, 0.1);
  color: #667eea;
  padding: 0.25rem 0.5rem;
  border-radius: 15px;
  font-size: 0.8rem;
  margin-left: 0.5rem;
}

.status-authorized {
  background: rgba(16, 185, 129, 0.1);
  color: #059669;
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 0.9rem;
  font-weight: 500;
}

.status-unauthorized {
  background: rgba(239, 68, 68, 0.1);
  color: #dc2626;
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 0.9rem;
  font-weight: 500;
}

.status-recognized {
  background: rgba(16, 185, 129, 0.1);
  color: #059669;
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 0.9rem;
  font-weight: 500;
}

/* Statistics */
.statistics {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.statistics h2 {
  margin-bottom: 2rem;
  color: #333;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.stat-card {
  background: white;
  border-radius: 15px;
  padding: 1.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-2px);
}

.stat-icon {
  font-size: 2rem;
}

.stat-value {
  font-size: 2rem;
  font-weight: 700;
  color: #333;
}

.stat-label {
  color: #666;
  font-size: 0.9rem;
}

.system-info {
  background: white;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
}

.system-info h3 {
  margin-bottom: 1.5rem;
  color: #333;
}

.info-grid {
  display: grid;
  gap: 1rem;
}

.info-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background: rgba(102, 126, 234, 0.05);
  border-radius: 10px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .dashboard-grid {
    grid-template-columns: 1fr;
  }
  
  .navigation {
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  
  .navigation button {
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
  }
  
  .table-header,
  .table-row {
    grid-template-columns: 1fr;
    gap: 0.5rem;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .video-controls {
    flex-direction: column;
  }
  
  .mode-buttons {
    flex-direction: column;
  }
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: rgba(102, 126, 234, 0.3);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(102, 126, 234, 0.5);
}

/* Fire Detection Styles */
.fire-alerts-banner {
  margin-bottom: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.fire-alert {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  border-radius: 10px;
  animation: pulse-alert 2s infinite;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.fire-alert.critical {
  background: linear-gradient(135deg, #dc2626 0%, #ef4444 100%);
  color: white;
  border: 2px solid #dc2626;
}

.fire-alert.high {
  background: linear-gradient(135deg, #f59e0b 0%, #fbbf24 100%);
  color: white;
  border: 2px solid #f59e0b;
}

.fire-alert.medium {
  background: linear-gradient(135deg, #fbbf24 0%, #fde047 100%);
  color: #333;
  border: 2px solid #fbbf24;
}

.fire-alert-icon {
  font-size: 2rem;
  animation: bounce 1s infinite;
}

.fire-alert-content {
  flex: 1;
}

.fire-alert-title {
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 0.25rem;
}

.fire-alert-details {
  font-size: 0.9rem;
  opacity: 0.95;
}

@keyframes pulse-alert {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.85; }
}

@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-5px); }
}

.fire-toggle,
.demographics-toggle {
  margin-top: 1rem;
  padding: 0.75rem;
  background: rgba(102, 126, 234, 0.05);
  border-radius: 10px;
}

.fire-toggle label,
.demographics-toggle label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-weight: 500;
}

.fire-toggle input[type="checkbox"],
.demographics-toggle input[type="checkbox"] {
  width: auto;
  margin-right: 0.5rem;
  cursor: pointer;
}

/* Fire log styles */
.log-item.fire {
  border-left-color: #dc2626;
  background: rgba(220, 38, 38, 0.05);
}

.fire-log {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.severity-badge {
  padding: 0.15rem 0.5rem;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 700;
  margin-left: 0.5rem;
}

.severity-badge.critical {
  background: #dc2626;
  color: white;
}

.severity-badge.high {
  background: #f59e0b;
  color: white;
}

.severity-badge.medium {
  background: #fbbf24;
  color: #333;
}

.severity-badge.low {
  background: #d1d5db;
  color: #333;
}

.alert-badge {
  padding: 0.15rem 0.5rem;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 700;
  background: #ef4444;
  color: white;
  margin-left: 0.5rem;
  animation: pulse-badge 1.5s infinite;
}

@keyframes pulse-badge {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.6; }
}

.severity-critical {
  color: #dc2626;
  font-weight: 700;
}

.severity-high {
  color: #f59e0b;
  font-weight: 600;
}

.severity-medium {
  color: #fbbf24;
  font-weight: 500;
}

.unknown-person {
  color: #6b7280;
  font-style: italic;
}

.demographics-info {
  font-size: 0.85rem;
  color: #666;
  margin-top: 0.25rem;
}

/* ===== MODERN THEME COMPONENTS ===== */

/* Modern Form Components */
.modern-form-container {
  max-width: 700px;
  margin: 2rem auto;
  padding: 2rem;
}

.modern-form-card {
  background: #111827;
  border: 1px solid #1f2937;
  border-radius: 16px;
  padding: 2.5rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.form-header {
  margin-bottom: 2rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #1f2937;
}

.form-header h2 {
  color: #f9fafb;
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
}

.form-header p {
  color: #9ca3af;
  font-size: 0.95rem;
}

.modern-message {
  padding: 1rem 1.25rem;
  border-radius: 10px;
  margin-bottom: 1.5rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.modern-message.success {
  background: rgba(16, 185, 129, 0.15);
  color: #10b981;
  border: 1px solid rgba(16, 185, 129, 0.3);
}

.modern-message.error {
  background: rgba(239, 68, 68, 0.15);
  color: #ef4444;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

.modern-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.modern-form .form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.modern-form label {
  color: #e5e7eb;
  font-weight: 500;
  font-size: 0.95rem;
}

.modern-form input,
.modern-form select,
.modern-form textarea {
  background: #1f2937;
  border: 1px solid #374151;
  border-radius: 8px;
  padding: 0.875rem 1rem;
  color: #f9fafb;
  font-size: 1rem;
  transition: all 0.2s ease;
}

.modern-form input:focus,
.modern-form select:focus,
.modern-form textarea:focus {
  outline: none;
  border-color: #60a5fa;
  background: #0f172a;
  box-shadow: 0 0 0 3px rgba(96, 165, 250, 0.1);
}

.modern-form input::placeholder,
.modern-form textarea::placeholder {
  color: #6b7280;
}

.modern-form textarea {
  resize: vertical;
  min-height: 80px;
  font-family: inherit;
}

.modern-form select {
  cursor: pointer;
}

.checkbox-group {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-top: 0.5rem;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  cursor: pointer;
  color: #e5e7eb;
  font-weight: 500;
}

.checkbox-label input[type="checkbox"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
  accent-color: #60a5fa;
}

.modern-submit-btn {
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: white;
  padding: 1rem 2rem;
  border: none;
  border-radius: 10px;
  font-size: 1.05rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(59, 130, 246, 0.3);
  margin-top: 1rem;
}

.modern-submit-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(59, 130, 246, 0.4);
}

.modern-submit-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

.modern-info-box {
  background: rgba(59, 130, 246, 0.1);
  border: 1px solid rgba(59, 130, 246, 0.2);
  border-radius: 12px;
  padding: 1.5rem;
  margin-top: 2rem;
}

.info-icon {
  color: #60a5fa;
  font-size: 1.5rem;
  margin-bottom: 0.75rem;
  display: block;
}

.info-content h3 {
  color: #60a5fa;
  font-size: 1.1rem;
  margin-bottom: 0.75rem;
}

.info-content p {
  color: #9ca3af;
  font-size: 0.95rem;
  line-height: 1.5;
}

/* Modern Logs Components */
.modern-logs-container {
  max-width: 1400px;
  margin: 2rem auto;
  padding: 2rem;
}

.logs-header {
  margin-bottom: 2rem;
}

.logs-header h2 {
  color: #f9fafb;
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.logs-header p {
  color: #9ca3af;
  font-size: 1rem;
}

.modern-filter-bar {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  flex-wrap: wrap;
}

.filter-btn {
  background: #111827;
  border: 1px solid #1f2937;
  color: #9ca3af;
  padding: 0.75rem 1.5rem;
  border-radius: 10px;
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.filter-btn:hover {
  background: #1f2937;
  border-color: #374151;
  color: #e5e7eb;
}

.filter-btn.active {
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  border-color: #3b82f6;
  color: white;
  box-shadow: 0 4px 15px rgba(59, 130, 246, 0.3);
}

.filter-icon {
  font-size: 1.1rem;
}

.modern-table-container {
  background: #111827;
  border: 1px solid #1f2937;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.modern-table-header {
  display: grid;
  grid-template-columns: 180px 140px 1fr 180px 140px;
  gap: 1rem;
  background: #1f2937;
  padding: 1.25rem 1.5rem;
  color: #9ca3af;
  font-weight: 600;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border-bottom: 1px solid #374151;
}

.modern-table-body {
  max-height: 600px;
  overflow-y: auto;
}

.modern-table-row {
  display: grid;
  grid-template-columns: 180px 140px 1fr 180px 140px;
  gap: 1rem;
  padding: 1.25rem 1.5rem;
  border-bottom: 1px solid #1f2937;
  transition: background 0.2s ease;
  align-items: center;
  color: #e5e7eb;
}

.modern-table-row:hover {
  background: #0f172a;
}

.modern-table-row:last-child {
  border-bottom: none;
}

.col-time {
  color: #9ca3af;
  font-size: 0.9rem;
}

.col-type {
  font-weight: 500;
}

.col-details {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.col-details strong {
  color: #f9fafb;
}

.col-demographics {
  color: #9ca3af;
  font-size: 0.9rem;
}

.col-status {
  text-align: right;
}

.confidence-badge {
  background: rgba(96, 165, 250, 0.15);
  color: #60a5fa;
  padding: 0.25rem 0.6rem;
  border-radius: 6px;
  font-size: 0.85rem;
  font-weight: 600;
  margin-left: 0.5rem;
}

.status-badge {
  display: inline-block;
  padding: 0.4rem 0.9rem;
  border-radius: 8px;
  font-size: 0.85rem;
  font-weight: 600;
  text-align: center;
}

.status-badge.recognized {
  background: rgba(16, 185, 129, 0.15);
  color: #10b981;
  border: 1px solid rgba(16, 185, 129, 0.3);
}

.status-badge.unknown {
  background: rgba(107, 114, 128, 0.15);
  color: #9ca3af;
  border: 1px solid rgba(107, 114, 128, 0.3);
}

.status-badge.authorized {
  background: rgba(16, 185, 129, 0.15);
  color: #10b981;
  border: 1px solid rgba(16, 185, 129, 0.3);
}

.status-badge.unauthorized {
  background: rgba(239, 68, 68, 0.15);
  color: #ef4444;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

.status-badge.alert {
  background: rgba(239, 68, 68, 0.15);
  color: #ef4444;
  border: 1px solid rgba(239, 68, 68, 0.3);
  animation: pulse-badge 1.5s infinite;
}

.status-badge.detected {
  background: rgba(251, 191, 36, 0.15);
  color: #fbbf24;
  border: 1px solid rgba(251, 191, 36, 0.3);
}

.no-logs {
  padding: 3rem 2rem;
  text-align: center;
  color: #6b7280;
  font-size: 1.1rem;
}

/* Modern Statistics Components */
.modern-statistics-container {
  max-width: 1400px;
  margin: 2rem auto;
  padding: 2rem;
}

.statistics-header {
  margin-bottom: 2rem;
}

.statistics-header h2 {
  color: #f9fafb;
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.statistics-header p {
  color: #9ca3af;
  font-size: 1rem;
}

.modern-stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.modern-stat-card {
  background: #111827;
  border: 1px solid #1f2937;
  border-radius: 16px;
  padding: 2rem;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.modern-stat-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  border-color: #374151;
}

.stat-card-icon {
  font-size: 2.5rem;
  line-height: 1;
}

.stat-card-content {
  flex: 1;
}

.stat-card-value {
  font-size: 2.5rem;
  font-weight: 700;
  color: #f9fafb;
  line-height: 1;
  margin-bottom: 0.5rem;
}

.stat-card-label {
  color: #9ca3af;
  font-size: 0.95rem;
  font-weight: 500;
}

.modern-system-info {
  background: #111827;
  border: 1px solid #1f2937;
  border-radius: 16px;
  padding: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.modern-system-info h3 {
  color: #f9fafb;
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

.system-info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
}

.info-card {
  background: #1f2937;
  border: 1px solid #374151;
  border-radius: 12px;
  padding: 1.5rem;
  transition: all 0.2s ease;
}

.info-card:hover {
  background: #0f172a;
  border-color: #4b5563;
}

.info-card-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 1rem;
}

.info-icon {
  font-size: 1.5rem;
}

.info-label {
  color: #9ca3af;
  font-size: 0.9rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.info-card-value {
  color: #f9fafb;
  font-size: 1.5rem;
  font-weight: 700;
}

.status-badge.active {
  background: rgba(16, 185, 129, 0.15);
  color: #10b981;
  border: 1px solid rgba(16, 185, 129, 0.3);
  padding: 0.4rem 0.9rem;
  border-radius: 8px;
  font-size: 0.95rem;
  font-weight: 600;
}

.status-badge.inactive {
  background: rgba(107, 114, 128, 0.15);
  color: #9ca3af;
  border: 1px solid rgba(107, 114, 128, 0.3);
  padding: 0.4rem 0.9rem;
  border-radius: 8px;
  font-size: 0.95rem;
  font-weight: 600;
}

/* Responsive Design for Modern Components */
@media (max-width: 1024px) {
  .modern-table-header,
  .modern-table-row {
    grid-template-columns: 140px 100px 1fr 140px 100px;
    gap: 0.75rem;
    padding: 1rem;
    font-size: 0.9rem;
  }
  
  .modern-stats-grid {
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  }
}

@media (max-width: 768px) {
  .modern-form-container,
  .modern-logs-container,
  .modern-statistics-container {
    padding: 1rem;
  }
  
  .modern-form-card {
    padding: 1.5rem;
  }
  
  .modern-filter-bar {
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .filter-btn {
    width: 100%;
    justify-content: center;
  }
  
  .modern-table-header {
    display: none;
  }
  
  .modern-table-row {
    grid-template-columns: 1fr;
    gap: 0.75rem;
    padding: 1rem;
    border: 1px solid #1f2937;
    border-radius: 12px;
    margin-bottom: 1rem;
  }
  
  .modern-table-body {
    padding: 1rem;
  }
  
  .col-time,
  .col-type,
  .col-details,
  .col-demographics,
  .col-status {
    padding: 0.5rem 0;
  }
  
  .col-time::before {
    content: 'Time: ';
    font-weight: 600;
    color: #9ca3af;
  }
  
  .col-type::before {
    content: 'Type: ';
    font-weight: 600;
    color: #9ca3af;
  }
  
  .col-demographics::before {
    content: 'Demographics: ';
    font-weight: 600;
    color: #9ca3af;
    display: block;
  }
  
  .col-status {
    text-align: left;
  }
  
  .col-status::before {
    content: 'Status: ';
    font-weight: 600;
    color: #9ca3af;
    display: block;
    margin-bottom: 0.5rem;
  }
  
  .modern-stats-grid {
    grid-template-columns: 1fr;
  }
  
  .system-info-grid {
    grid-template-columns: 1fr;
  }
}

/* Smooth Scrollbar for Modern Tables */
.modern-table-body::-webkit-scrollbar {
  width: 8px;
}

.modern-table-body::-webkit-scrollbar-track {
  background: #1f2937;
}

.modern-table-body::-webkit-scrollbar-thumb {
  background: #374151;
  border-radius: 10px;
}

.modern-table-body::-webkit-scrollbar-thumb:hover {
  background: #4b5563;
}